"use client";

import { useParams } from "next/navigation";
import { useEffect, useState } from "react";
import { Layout, Move, Square, DoorOpen, Maximize2, Type, Eraser, Grid, Layers } from "lucide-react";
import { cn } from "@/lib/utils";
import { Card, CardContent, Button } from "@/components/ui";
import { SvgCanvas } from "@/components/plan-editor";
import { apiClient } from "@/lib/api-client";
import { useUIStore } from "@/stores/ui-store";
import { useProjectStore } from "@/stores/project-store";

const tools = [
  { id: "select", icon: Move, label: "Select", labelAr: "تحديد" },
  { id: "wall", icon: Square, label: "Wall", labelAr: "جدار" },
  { id: "door", icon: DoorOpen, label: "Door", labelAr: "باب" },
  { id: "window", icon: Maximize2, label: "Window", labelAr: "نافذة" },
  { id: "text", icon: Type, label: "Text", labelAr: "نص" },
  { id: "erase", icon: Eraser, label: "Erase", labelAr: "مسح" },
];

const layers = [
  { id: "architectural", label: "Architectural", labelAr: "معماري", color: "#1a1c1e" },
  { id: "structural", label: "Structural", labelAr: "هيكلي", color: "#0f4c81" },
  { id: "mep", label: "MEP", labelAr: "ميكانيكي", color: "#1b6b61" },
  { id: "annotations", label: "Annotations", labelAr: "تعليقات", color: "#b36b2d" },
];

export default function PlanEditorPage() {
  const params = useParams();
  const { language } = useUIStore();
  const { project, run, setProject } = useProjectStore();
  const isRtl = language === "ar";
  const projectId = Number(params.id);
  const [planFile, setPlanFile] = useState<string | null>(null);
  const [activeTool, setActiveTool] = useState("select");

  useEffect(() => {
    const loadProject = async () => {
      const found = await apiClient.getProject(projectId);
      if (found) {
        setProject(found);
      }
    };
    loadProject();
  }, [projectId, setProject]);

  // Load plan file from artifacts
  useEffect(() => {
    const loadArtifacts = async () => {
      if (!run?.id) return;
      try {
        const artifacts = await apiClient.getRunArtifacts(run.id);
        const planArtifact = artifacts.find(
          (a) => a.kind === "plan_svg" || a.file_name?.endsWith("_plan.svg")
        );
        if (planArtifact) {
          setPlanFile(planArtifact.file_name);
        }
      } catch (error) {
        console.error("Failed to load artifacts:", error);
      }
    };
    loadArtifacts();
  }, [run?.id]);

  return (
    <div className="space-y-4 animate-fade-in">
      {/* Page Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <p className="text-xs font-bold uppercase tracking-[3px] text-[var(--accent-2)] mb-1">
            {project?.name || (isRtl ? "المشروع" : "Project")}
          </p>
          <h1 className="text-2xl font-bold text-[var(--ink)]">
            {isRtl ? "محرر المخطط" : "Plan Editor"}
          </h1>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Grid className="w-4 h-4 mr-2" />
            {isRtl ? "الشبكة" : "Grid"}
          </Button>
          <Button variant="outline" size="sm">
            <Layers className="w-4 h-4 mr-2" />
            {isRtl ? "الطبقات" : "Layers"}
          </Button>
        </div>
      </div>

      {/* CAD Workspace */}
      <div className="grid grid-cols-[72px_1fr_280px] gap-4">
        {/* Tools Palette */}
        <Card className="h-fit">
          <CardContent className="p-2">
            <div className="space-y-2">
              {tools.map((tool) => {
                const Icon = tool.icon;
                return (
                  <button
                    key={tool.id}
                    onClick={() => setActiveTool(tool.id)}
                    className={cn(
                      "w-12 h-12 rounded-[var(--radius-sm)] border flex items-center justify-center transition-all",
                      tool.id === activeTool
                        ? "bg-[rgba(15,76,129,0.12)] border-[var(--accent)] text-[var(--accent)]"
                        : "bg-[var(--surface-2)] border-[var(--line)] text-[var(--ink-soft)] hover:border-[var(--accent)]"
                    )}
                    title={isRtl ? tool.labelAr : tool.label}
                  >
                    <Icon className="w-5 h-5" />
                  </button>
                );
              })}
            </div>
          </CardContent>
        </Card>

        {/* Canvas */}
        <Card className="min-h-[600px]">
          <CardContent className="p-4 h-full">
            <div className="w-full h-full bg-[#f8f6f1] rounded-[var(--radius-md)] border border-[var(--line)] relative overflow-hidden"
              style={{
                backgroundImage: `
                  linear-gradient(0deg, rgba(100,110,125,0.14) 1px, transparent 1px),
                  linear-gradient(90deg, rgba(100,110,125,0.14) 1px, transparent 1px)
                `,
                backgroundSize: "24px 24px",
              }}
            >
              {/* Placeholder */}
              <div className="absolute inset-0 flex items-center justify-center">
                <div className="text-center text-[var(--ink-faint)]">
                  <Layout className="w-16 h-16 mx-auto mb-4 opacity-30" />
                  <p className="text-lg font-medium">
                    {isRtl ? "ابدأ التوليد لعرض المخطط" : "Start generation to view plan"}
                  </p>
                  <p className="text-sm mt-2">
                    {isRtl
                      ? "استخدم الأدوات على اليسار للتعديل"
                      : "Use tools on the left to edit"}
                  </p>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Properties Panel */}
        <div className="space-y-4">
          {/* Layers */}
          <Card>
            <CardContent className="p-4">
              <h3 className="font-semibold text-[var(--ink)] mb-3">
                {isRtl ? "الطبقات" : "Layers"}
              </h3>
              <div className="space-y-2">
                {layers.map((layer) => (
                  <label
                    key={layer.id}
                    className="flex items-center gap-3 p-2 rounded-[var(--radius-sm)] border border-[var(--line)] bg-[var(--surface-2)] cursor-pointer hover:bg-[var(--bg-2)]"
                  >
                    <input type="checkbox" defaultChecked className="accent-[var(--accent)]" />
                    <span
                      className="w-3 h-3 rounded-full"
                      style={{ backgroundColor: layer.color }}
                    />
                    <span className="text-sm text-[var(--ink)]">
                      {isRtl ? layer.labelAr : layer.label}
                    </span>
                  </label>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Properties */}
          <Card>
            <CardContent className="p-4">
              <h3 className="font-semibold text-[var(--ink)] mb-3">
                {isRtl ? "الخصائص" : "Properties"}
              </h3>
              <p className="text-sm text-[var(--ink-soft)]">
                {isRtl
                  ? "حدد عنصرًا لرؤية خصائصه"
                  : "Select an element to view properties"}
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
